import { Injectable } from '@angular/core';
/*import { Roles } from '../Models/roles.enum';*/
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { environment } from '../../environments/environment.prod';
import jwt_decode, { JwtPayload } from  'jwt-decode';
import { Router } from '@angular/router';
//import { decode } from 'querystring';
//import * as decode from 'jwt-decode'

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  authStatus = new BehaviorSubject<Authorizer>(JSON.parse(localStorage.getItem("auth")) || defaultAuthStatus);
  authProvider: (email: string, pass: string) => Observable<Token>;
  constructor(private http: HttpClient, private route: Router) {
    this.authProvider = this.UserauthProvider;
  }

  UserauthProvider(email: string, pass: string): Observable<Token> {
    return this.http.post<Token>(environment.urlServices + "Token/Post", { email: email, password: pass });
  }
  async login(email: string, pass: string) {
    let res;
    try {
      res = await this.UserauthProvider(email, pass).toPromise().catch(err => this.ErrorToken(err))//.subscribe(x => res = this.ConfirmToken(x), err => this.ErrorToken(err));
      res = this.ConfirmToken(res);
    } catch (e) {

    }

    return res;
  }
  ConfirmToken(x: Token): Token {
      let dd = this.LoginDecode(x.access_Token);
      return x;
    }
    ErrorToken(err: any): void {
        alert(err.error.mjs)
    }

  LoginDecode(token: string): any {
    this.logout();
    localStorage.setItem("jwt", token);
    const res = jwt_decode<Authorizer>(token);
    localStorage.setItem("auth", JSON.stringify(res));
    this.authStatus.next(res);
    return res;
  }
  getToken(): string {
    try {
      return localStorage.getItem("jwt").toString();
    } catch (e) {
      return null;
    }

  }
  getTokenAndAuth() {
    try {
      var autho = JSON.parse(localStorage.getItem("auth"));
      autho.access_Token = this.getToken();

      return autho;
    } catch (e) {
      localStorage.clear();
      return false
    }
  }
  logout() {
    this.authStatus.next(defaultAuthStatus);
    localStorage.clear();
    this.route.navigate(["Login"]);
  }
}


class Token {
  access_Token: string;
  statusCode: number;
  expires_In: number;
  token_Type: string;
}
class Authorization {
  unique_name: string;
  role: number;
  userMainId: string;
  nbf: string;
  exp: string;
  iat: string;
  iss: string;
  aud: string;
  userHelperId: number;
  access_Token: string;
}

export interface Authorizer {
  unique_name: string;
  role: null;
  userMainId: string;
  userHelperId: string;
}
const defaultAuthStatus: Authorizer = { role: null, userMainId: null, unique_name: null, userHelperId: null};
